import { Badge } from '@/components/ui/badge';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Code2, Database, Layout, Server, Terminal, PenTool, CheckCircle2 } from 'lucide-react';

interface Skill {
  _id: string;
  category: string;
  skills: string[];
}

// Map categories to specific premium styling and icons
const getCategoryStyle = (category: string) => {
  switch (category) {
    case 'Programming Languages':
      return { color: 'text-blue-600 dark:text-blue-400', bg: 'bg-blue-500/10', border: 'border-blue-200/50', icon: Code2 };
    case 'Frameworks & Libraries':
      return { color: 'text-purple-600 dark:text-purple-400', bg: 'bg-purple-500/10', border: 'border-purple-200/50', icon: Layout };
    case 'Databases':
      return { color: 'text-green-600 dark:text-green-400', bg: 'bg-green-500/10', border: 'border-green-200/50', icon: Database };
    case 'Tools & Platforms':
      return { color: 'text-orange-600 dark:text-orange-400', bg: 'bg-orange-500/10', border: 'border-orange-200/50', icon: Terminal };
    case 'Cloud & DevOps':
      return { color: 'text-red-600 dark:text-red-400', bg: 'bg-red-500/10', border: 'border-red-200/50', icon: Server };
    case 'Design':
      return { color: 'text-pink-600 dark:text-pink-400', bg: 'bg-pink-500/10', border: 'border-pink-200/50', icon: PenTool };
    case 'Testing':
      return { color: 'text-indigo-600 dark:text-indigo-400', bg: 'bg-indigo-500/10', border: 'border-indigo-200/50', icon: CheckCircle2 };
    default:
      return { color: 'text-gray-600', bg: 'bg-gray-500/10', border: 'border-gray-200/50', icon: Code2 };
  }
};

const Skills = ({ skills }: { skills: Skill[] }) => {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
      {skills.map((skill) => {
        const style = getCategoryStyle(skill.category);
        const Icon = style.icon;

        return (
          <Card key={skill._id} className="border-0 bg-white/40 dark:bg-white/5 backdrop-blur-md shadow-lg ring-1 ring-white/20 transition-all hover:scale-[1.02]">
            <CardHeader className="pb-3 border-b border-white/10">
              <CardTitle className={`text-lg font-bold flex items-center gap-2 ${style.color}`}>
                <div className={`p-2 rounded-lg ${style.bg}`}>
                  <Icon className="w-5 h-5" />
                </div>
                {skill.category}
              </CardTitle>
            </CardHeader>
            <CardContent className="pt-4">
              <div className="flex flex-wrap gap-2">
                {skill.skills.map((skillName, index) => (
                  <Badge
                    key={index}
                    variant="outline"
                    className={`
                      px-3 py-1 text-sm font-medium transition-all duration-300
                      ${style.bg} ${style.color} ${style.border}
                      hover:bg-opacity-30 hover:scale-105 backdrop-blur-sm
                    `}
                  >
                    {skillName}
                  </Badge>
                ))}
              </div>
            </CardContent>
          </Card>
        );
      })}
    </div>
  );
};

export default Skills;