import Image from 'next/image';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Mail, Linkedin, Github, Phone, ExternalLink, MapPin } from 'lucide-react';

interface Profile {
  name: string;
  title: string;
  location: string;
  summary: string;
  high_res_image_url: string;
  contact: {
    email: string;
    phone: string;
    linkedin: string;
    github: string;
    portfolio: string;
  };
}

const socialLinks = [
  { name: 'Email', icon: Mail, href: (p: Profile) => `mailto:${p.contact.email}`, variant: 'default' as const },
  { name: 'LinkedIn', icon: Linkedin, href: (p: Profile) => p.contact.linkedin, variant: 'ghost' as const }, // Changed to ghost for minimal look
  { name: 'GitHub', icon: Github, href: (p: Profile) => p.contact.github, variant: 'ghost' as const },
  { name: 'Portfolio', icon: ExternalLink, href: (p: Profile) => p.contact.portfolio, variant: 'ghost' as const },
];

const Hero = ({ profile }: { profile: Profile }) => {
  return (
    <Card className="border-0 bg-white/40 dark:bg-black/20 backdrop-blur-xl shadow-2xl rounded-3xl overflow-hidden ring-1 ring-white/40">
      <CardContent className="p-8 md:p-12 flex flex-col md:flex-row items-center gap-10">
        
        {/* Image Container with Soft Glow */}
        <div className="relative group">
          <div className="absolute -inset-1 bg-gradient-to-r from-blue-400 to-purple-400 rounded-full blur opacity-20 group-hover:opacity-40 transition duration-500"></div>
          <div className="relative w-48 h-48 md:w-64 md:h-64 rounded-full overflow-hidden border-4 border-white/50 shadow-lg">
            <Image
              src={profile.high_res_image_url}
              alt={profile.name}
              fill
              className="object-cover"
              priority
            />
          </div>
        </div>
        
        {/* Text Content */}
        <div className="flex-1 text-center md:text-left space-y-4">
          <div>
            <h1 className="text-4xl md:text-5xl font-extrabold tracking-tight bg-clip-text text-transparent bg-gradient-to-r from-gray-900 to-gray-600 dark:from-white dark:to-gray-300">
              {profile.name}
            </h1>
            <p className="text-xl font-medium text-primary/80 mt-2">{profile.title}</p>
            
            <div className="flex items-center justify-center md:justify-start gap-4 mt-3 text-sm text-muted-foreground">
              <span className="flex items-center bg-white/30 px-3 py-1 rounded-full">
                <MapPin className="w-4 h-4 mr-1" />
                {profile.location}
              </span>
              {profile.contact.phone && (
                <span className="flex items-center bg-white/30 px-3 py-1 rounded-full">
                  <Phone className="h-4 w-4 mr-1" />
                  {profile.contact.phone}
                </span>
              )}
            </div>
          </div>
          
          <p className="text-lg text-muted-foreground leading-relaxed max-w-2xl">
            {profile.summary}
          </p>
          
          <div className="flex flex-wrap justify-center md:justify-start gap-3 pt-4">
            {socialLinks.map(({ name, icon: Icon, href, variant }) => (
              <Button
                key={name}
                variant={variant}
                size="sm"
                className={`gap-2 rounded-full transition-all hover:scale-105 ${variant === 'ghost' ? 'bg-white/20 hover:bg-white/40' : ''}`}
                asChild
              >
                <a href={href(profile)} target={name !== 'Email' ? '_blank' : undefined} rel="noopener noreferrer">
                  <Icon className="h-4 w-4" />
                  <span>{name}</span>
                </a>
              </Button>
            ))}
          </div>
        </div>
      </CardContent>
    </Card>
  );
};

export default Hero;